import axios from 'axios';
import Vue from 'vue';
import store from '@/store';
import jsonBig from 'json-bigint';
import { Toast, Dialog } from 'vant';
import router from '@/router';

// token
const refreshToken = axios.create({
  baseURL: 'http://ttapi.research.itcast.cn/',
});

// 
router.beforeEach((to, from, next) => {
  // 
  if (to.meta.requireAuth) {
    // 
    if (!store.state.userToken) {
      Dialog.confirm({
        title: '',
        message: ',',
      })
        .then(() => router.push({

          path: '/login',
          query: {
            toPath: from.fullPath,
          },

        }))
        .catch(() => {

        });
    } else {
      next();
    }
  } else {
    next();
  }
});

// 
function redirectLogin() {
  router.replace({
    name: 'Login',
    query: {
      toPath: router.currentRoute.fullPath,
    },
  });
}

export const request = axios.create({
  baseURL: 'http://ttapi.research.itcast.cn/',
  transformResponse: [function (data) {
    try {
      // 
      return jsonBig.parse(data);
    } catch (err) {
      // 
      return {
        data,
      };
    }
  }],
});
// 
request.interceptors.request.use((req) => {
  const { userToken } = store.state;
  if (userToken) {
    req.headers.Authorization = `Bearer ${userToken.token}`;
  }
  return req;
}, (e) => Promise.reject(e));

// 
request.interceptors.response.use((res) => res, async (e) => {
  const { status } = e.response;
  if (status === 400) {
    Toast.fail('');

    // token
  } else if (status === 401) {
    const { userToken } = store.state;
    //  token
    if (!userToken || !userToken.token) {
      redirectLogin();// token 
    } else {
      try { // token token
        const { data } = await refreshToken({
          url: '/app/v1_0/authorizations',
          method: 'put',
          headers: {
            Authorization: `Bearer ${userToken.refresh_token}`,
          },
        });
        // token store
        store.commit('setToken', {
          token: data.data.token,
          refresh_token: userToken.refresh_token,
        });
        return request(e.config);//  401
      } catch (e) {
        // token 
        redirectLogin();
      }
    }
  } else if (status === 403) {
    Toast.fail('');
  } else if (status >= 500) {
    Toast.fail(',');
  }
  return Promise.reject(e);
});
